require('dotenv/config');
// import { v4 as uuidv4 } from 'uuid';
import users from "./db";

// Database
import { mySQLQuery, connection } from './mySQL/database';

const resolvers = {
  Query: {
    user: (parent, { email_address }, context, info) => {     
      return users.find(user => user.email_address === email_address)
    },

    users: (parent, args, context, info) => {
      return users;
    }
  },

  Mutation: {
    createUser: (parent, { id, name, email_address, password }, context, info) => {
      // const userAlreadyExists = users.some(user => user.email_address === email_address);
      // if (userAlreadyExists) throw new Error ('User already exists.');
      
      // const newUser = { id, name, email_address, password };

      // users.push(newUser);

      // return newUser;

      connection.connect(err => {
        if (err) throw err;
        console.log('Connected');
        mySQLQuery(connection, `INSERT INTO users(id, name, email_address, password) 
          VALUES (${id}, ${name}, ${email_address}, ${password})`)
      });
    },
    // updateUser: (parent, { id, name, email, age }, context, info) => {
    //   const newUser = users.find(user => user.id === id);

    //   newUser.name = name;
    //   newUser.email = email;
    //   newUser.age = age;

    //   return newUser;
    // },
    deleteUser: (parent, { email_address }, context, info) => {
      const userIndex = users.findIndex(user => user.email_address === email_address);

      if (userIndex === -1) throw new Error("User not found.");

      const deletedUsers = users.splice(userIndex, 1);

      return deletedUsers[0];
    },

    createBook: (parent, { email_address, title, author, genre, description }, context, info) => {
      const newBook = { title, author, genre, description };
      const user = users.find(user => user.email_address === email_address)

      const bookAlreadyExists = user.books.some(book => book.title.toLowerCase() === newBook.title.toLowerCase())
      if (bookAlreadyExists) throw new Error('Book already exists.')
      
      user.books.push(newBook)
    }
  }
};

export default resolvers;